.form-main{
    width: 400px;
    background-color: rgba(255,255,255,0.13);
    position: absolute;
    overflow: auto;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255,255,255,0.1);
    box-shadow: 0 0 40px rgba(8,7,16,0.6);
    padding: 50px 35px;
    max-height: calc(100vh - 130px);
}
.form-main *{
    font-family: 'Poppins',sans-serif;
    color: #ffffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
}
.form-main h3{
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
}

.form-main__item{
    padding: 18px 0;
    position: relative;
}

.form-main__item__error{
    color: #c24e4e;
    position: absolute;
    bottom: 0;
}

.form-main__item__label{
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
}
.form-main__item__input{
    display: block;
    height: 50px;
    width: 100%;
    background-color: rgba(255,255,255,0.07);
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
}
::placeholder{
    color: #e5e5e5;
}
.form-main__button{
    margin-top: 50px;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}
.form-main__social{
    margin-top: 30px;
    display: flex;
}
.form-main__social a{
    background: red;
    width: 150px;
    border-radius: 3px;
    padding: 5px 10px 10px 5px;
    background-color: rgba(255,255,255,0.27);
    color: #eaf0fb;
    text-align: center;
}
.form-main__social div:hover{
    background-color: rgba(255,255,255,0.47);
}
.form-main__social .fb{
    margin-left: 25px;
}
.form-main__social i{
    margin-right: 4px;
}